import React from 'react';
import Button from './button'

function NoteArea(props) {
    let title, content;
    if (props.currentNoteData !== null) {
        title = props.currentNoteData.title;
        content = props.currentNoteData.content;
    }

    let className = 'view';
    if(props.activeView === 'noteArea') {
        className += ' -active'
    }
    
    return(
        <div className={className}>
            <Button label="Notes" function={props.toggleMenu} position={'top-left'}/>
            <Button label="Options" function={props.toggleDropDown} position={'top-right'}/>
            <div className='note-area'>
                <textarea value={title} className="note-title" onChange={(event) => props.handleTitleChange(event, props.noteKey)}></textarea>
                <textarea value={content} className="note" onChange={props.handleChange} placeholder="Write somethingâ€¦" onFocus={(event) => props.startSaveTimer(event, 'contentTimer')} onBlur={() => props.removeSaveTimer('contentTimer')}></textarea>
            </div>
        </div>
    )
}

export default NoteArea